
import Scroll from '../../../base/scroll/scroll'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'

import { mapActions } from 'vuex'
export default {
name: "userQq",
components: {
Scroll,
XInput,
Group
},
watch:{
addressQQ(newVal){
if(newVal.length>0){
this.btnSaveState = true
return false
}
this.btnSaveState = false
}
},
data() {
return {
addressQQ:'',
btnSaveState:false
}
},

methods: {

saveQq(){//addressQQ
this.$axios.post('/api/user/update', {
telephone: this.$store.state.userInfo.telephone,
qqNum: this.addressQQ
}).then((response) => {
console.log(response)
let that = this;
let obj = this.$store.state.userInfo;
obj.qqNum = this.addressQQ;
this.setUserInfo(obj);
this.$vux.toast.show({
text: '',
onHide () {
that.$router.push({ name: 'settings' })
}
})
}).catch((error) => {
console.log(error)
})
},
...mapActions([
'setUserInfo'
])
}
}



